
import tools from '../../lib/tools.js';
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
name: 'login',
data() {
return {
showToast:false,
toastMsg: '',
toastType:'text',
showWarn:false,
mobile:'',
randCode:'',
isDisabled:false,

btnText:'发送验证码',
twoClick:false
}
},
components:{Group,XInput,XButton,Toast},
created(){
let tempURL = tools.getRequestParam();
tempURL.mobile && (this.mobile = tempURL.mobile);
tempURL.randCode && (this.randCode = tempURL.randCode);
if(tempURL.message){
this.showToast = true;
this.toastMsg = decodeURI(tempURL.message);
}
},
methods:{
loging(){
if(this.twoClick){return;}
this.twoClick = !this.twoClick;
if(!this.mobile || !(/^1(3|4|5|7|8)\d{9}$/.test(this.mobile))){
this.showToast = true;
this.toastMsg = '请输入正确的手机号';
return
}
if(!this.randCode || this.randCode.length!=6){
this.showToast = true;
this.toastMsg = '请输入正确验证码';
return
}
var vm = this;

let tempURL = sessionStorage.getItem('tempURL');
if(!tempURL){
tempURL = '/m/index.html';
}
document.getElementById('url').value = encodeURI(tempURL);
document.getElementById('loginForm').submit();

return;



this.$http.post('/api/WeiXin/auth.action',{type:1,url:tempURL,jsonData:{mobile:vm.mobile,randCode:vm.randCode}}).then((result) => {
let data = result.data;
if(data.successful){
if(data.resultCode.code==='SUCCESS'){
localStorage.setItem('md',data.data.managerId);// 写入userid
let url = data.data.url || sessionStorage.getItem('tempURL');
if(url){
vm.twoClick = false;
sessionStorage.removeItem('tempURL');
tools.next(url);
}
tools.next("/m/index.html?md="+data.data.managerId);

}else{
vm.twoClick = false;
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
vm.isDisabled = false;
console.log(data);
}
}else{
vm.twoClick = false;
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
vm.isDisabled = false;
console.log(data);
}
},(error) => {
console.log(error);
});
},
randCodeFn(e){
if(this.isDisabled){
return false;
}
if(!this.mobile || !(/^1(3|4|5|7|8)\d{9}$/.test(this.mobile))){
this.showToast = true;
this.toastMsg = '请输入正确的手机号';
return
}
var vm = this;
let num=60;
let timer = setInterval(function(){
num--;
vm.btnText = num+'秒后可发送';
console.log(num);
if(num<=0){
vm.isDisabled = false;
vm.btnText = '获取验证码';
clearInterval(timer);
}
},1000);
vm.isDisabled = true;
this.$http.post('/apiUser/randCode?mobile='+this.mobile,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200) && data.resultCode){
if(data.resultCode.code==='SUCCESS'){
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}else{
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
console.log(data);
vm.isDisabled = false;
vm.btnText = '获取验证码';
clearInterval(timer);
}
}else{
console.log(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
vm.isDisabled = false;
vm.btnText = '获取验证码';
clearInterval(timer);
}
},(error) => {
console.log(error);
vm.showToast = true;
vm.toastMsg = '发生错误';
vm.isDisabled = false;
vm.btnText = '获取验证码';
clearInterval(timer);
}).catch((error) => {
console.log('--',error);
vm.showToast = true;
vm.toastMsg = '发生错误';
vm.isDisabled = false;
vm.btnText = '获取验证码';
clearInterval(timer);
});
this.toastMsg = '';
e.stopPropagation();
e.preventDefault();
return false;
},
intervalFn(){

}
}
}



